// AES-CBC
function decrypt(text) {
	// console.log("====text===",text);
	const key = "ax7x90.3k_10li5u"; //
	const iv = "a0.l954b_107x90l"; //
	let baseResult = CryptoJS.enc.Base64.parse(text); // Base64
	let ciphertext = CryptoJS.enc.Base64.stringify(baseResult); // Base64
	let decryptResult = CryptoJS.AES.decrypt(ciphertext, CryptoJS.enc.Utf8.parse(key), { //  AES
		iv: CryptoJS.enc.Utf8.parse(iv),
		mode: CryptoJS.mode.CBC,
		padding: CryptoJS.pad.Pkcs7
	});
	let resData = decryptResult.toString(CryptoJS.enc.Utf8).toString();
	// console.log("====resData===",resData)
	return resData;
}

// yyyy-MM-dd HH:mm:ssyyyy-MM-dd
function getClientNowSystemTime(format, time) {
	let date = '';
	if (time) {
		date = new Date(time)
	} else {
		date = new Date()
	}
	let year = date.getFullYear(); //
	let month = date.getMonth() + 1; //
	if (month >= 1 && month <= 9) {
		month = "0" + month;
	}
	let day = date.getDate(); //
	if (day >= 1 && day <= 9) {
		day = "0" + day;
	}
	let hour = date.getHours(); //
	if (hour >= 1 && hour <= 9) {
		hour = "0" + hour;
	}
	let minute = date.getMinutes(); //
	if (minute >= 1 && minute <= 9) {
		minute = "0" + minute;
	}
	let second = date.getSeconds(); //
	if (second >= 1 && second <= 9) {
		second = "0" + second;
	}
	let ms = date.getMilliseconds(); //
	if (ms >= 1 && ms <= 9) {
		ms = "00" + ms;
	}
	if (ms >= 10 && ms <= 99) {
		ms = "0" + ms;
	}
	if (!format) {
		console.debug(
			"timeutils-getClientNowSystemTime",
			"yyyyMMdd"
		);
		return year + "" + month + "" + day;
	}
	let result = format;
	if (result.indexOf("y") != -1) {
		result = result.replace("yyyy", year);
	}
	if (result.indexOf("M") != -1) {
		result = result.replace("MM", month);
	}
	if (result.indexOf("d") != -1) {
		result = result.replace("dd", day);
	}
	if (result.indexOf("H") != -1) {
		result = result.replace("HH", hour);
	}
	if (result.indexOf("m") != -1) {
		result = result.replace("mm", minute);
	}
	if (result.indexOf("s") != -1) {
		result = result.replace("ss", second);
	}
	if (result.indexOf("S") != -1) {
		result = result.replace("SSS", ms);
	}
	return result;
}
/**
 * 
 * @param {*} sId 
 */
function isCardID(sId) {
	if (!/(^\d{15}$)|(^\d{17}(\d|X|x)$)/.test(sId)) {

		return false
	}
	//
	var aCity = {
		11: "",
		12: "",
		13: "",
		14: "",
		15: "",
		21: "",
		22: "",
		23: "",
		31: "",
		32: "",
		33: "",
		34: "",
		35: "",
		36: "",
		37: "",
		41: "",
		42: "",
		43: "",
		44: "",
		45: "",
		46: "",
		50: "",
		51: "",
		52: "",
		53: "",
		54: "",
		61: "",
		62: "",
		63: "",
		64: "",
		65: "",
		71: "",
		81: "",
		82: "",
		91: ""
	};
	if (!aCity[parseInt(sId.substr(0, 2))]) {

		return false
	}

	// 
	var sBirthday = (sId.substr(6, 4) + "-" + Number(sId.substr(10, 2)) + "-" + Number(sId.substr(12, 2))).replace(/-/g,
			"/"),
		d = new Date(sBirthday)
	if (sBirthday != (d.getFullYear() + "/" + (d.getMonth() + 1) + "/" + d.getDate())) {

		return false
	}

	// 
	var sum = 0,
		weights = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2],
		codes = "10X98765432"
	for (var i = 0; i < sId.length - 1; i++) {
		sum += sId[i] * weights[i];
	}
	var last = codes[sum % 11]; //
	if (sId[sId.length - 1] != last) {

		return false
	}

	return true
}
/**
 * Bse64
 * @param {*} tempImagePath=========>
 */
function getBase64(tempImagePath) {
	return new Promise(function(resolve, reject) {
		wx.getFileSystemManager().readFile({
			filePath: tempImagePath,
			encoding: "base64",
			success: function(data) {
				resolve(data)
			}
		})
	})
}
//------->
function MouseMovement() {
	// 
	const movement = reactive({
		w: window.pageXOffset,
		h: window.pageYOffset
	});
	mounted(() => {
		// 
		window.addEventListener("mousemove", function() {
			movement.w = event.pageX;
			movement.h = event.pageY;
		})
	})
	// size
	return movement;
}
