// wx.request
const request = (url, options) => {
  return new Promise((resolve, reject) => {
    uni.request({
      url: `${getApp().globalData.host}${url}`,//
      method: options.method, //method
      data: options.method === 'GET' ? JSON.stringify(options.data):options.data, 
      //GET,GETquery String,options.data
      header: {
        "Content-Type": "application/x-www-form-urlencoded;charset=utf-8",
		'Content-Type':'application/x-www-form-urlencoded'
        //  'token':token                     
      },
      //headertoken
      success(request) {
        // 
        // 
        // 
      //
        if (request.statusCode === 200) {
        //10000,request.dataresolve
          resolve(request.data)
        } else {
        //,request.datareject
          reject(request.data)
        }
      },
      fail(error) {
      //reject()
        reject(error.data)
      }
    })
  })
  
  
  
}
//get
const get = (url, options = {}) => {
  return request(url, {
    method: 'GET',
    data: options
  })
}
//post
const post = (url, options) => {
  return request(url, {
    method: 'POST',
    data: options
  })
}
//put
const put = (url, options) => {
  return request(url, {
    method: 'PUT',
    data: options
  })
}
//remove
// DELETE
const remove = (url, options) => {
  return request(url, {
    method: 'DELETE',
    data: options
  })
}
//wx.requestpost,get,put,remove
module.exports = {
  get,
  post,
  put,
  remove
}